.question {
  border: 1px solid #bdbdbd;
  border-radius: 5px;
  -webkit-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.08);
  box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.08);
  background-color: #f5f5f5;
}

.question__title {
  font-size: 1.2em;
}

.question__title--description {
  font-weight: 200;
  margin-right: 5px;
}

.question__title--value:hover {
  color: #424242;
}

.question__title--bold {
  font-weight: bold;
}

.question__link {
  cursor: pointer;
  color: #3f51b5;
}

.question__link--bottom {
  cursor: pointer;
  color: #3f51b5;
  margin-top: 10px;
}

.question__details {
  color: #3f51b5;
}

.question__choices {
  border: solid 1px #e0e0e0;
}
